.options-calendar {
  position: relative;
  display: block;
  width: 100%;
  margin: 0;
  padding: 17px 22px 22px;
  border: 0;
  background: #ebedf2;
  border-radius: 5px; }
  .options-calendar + .options-calendar {
    margin-top: 30px; }
  .options-calendar-field + .options-calendar-field {
    margin-top: 20px; }
  .options-calendar-label {
    display: block;
    width: 100%;
    font-weight: 500; }
  .options-calendar-input {
    margin-top: 3px;
    padding: 6px 8px;
    border: 1px solid #aeb4bf;
    border-radius: 3px;
    transition: border-color 75ms linear;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400; }
    .options-calendar-input:focus {
      outline: none;
      border-color: #4c7bd9; }
    .options-calendar-input--url {
      width: 100%; }
  .options-calendar-error {
    display: block;
    margin-top: 15px;
    font-size: 14px;
    line-height: 21px;
    color: #f95133; }
  .options-calendar--hasError {
    box-shadow: 0 0 4px #fa8575; }
  .options-calendar-remove {
    background: transparent;
    border: 0;
    padding: 0;
    cursor: pointer;
    display: block;
    position: absolute;
    overflow: hidden;
    top: 10px;
    right: 10px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    font-size: 22px;
    line-height: 21.6px;
    text-align: center;
    background: #ccd0d9;
    transition: background-color 25ms linear, color 25ms linear; }
    .options-calendar-remove:hover {
      background: #e84832;
      color: #ffffff; }
    .options-calendar-remove:focus {
      outline: none; }
    .options-calendar-remove-icon {
      display: block;
      width: 100%;
      height: 100%; }
    .options-calendar-remove-text {
      position: absolute;
      top: 24px; }
