.oknf-switch {
  display: block;
  position: relative;
  padding-left: 43px;
  height: 26px;
  line-height: 26px;
  cursor: pointer; }
  .oknf-switch input {
    position: absolute;
    top: calc(50% - 5px);
    left: 21.5px; }
  .oknf-switch--has-label {
    padding-left: 56px; }
  .oknf-switch:active .oknf-switch-decoration,
  .oknf-switch input:focus ~ .oknf-switch-decoration {
    background: #a0a7b4; }
  .oknf-switch input:checked:active ~ .oknf-switch-decoration,
  .oknf-switch input:checked:focus ~ .oknf-switch-decoration {
    background: #1bab67; }
  .oknf-switch-decoration {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 43px;
    height: 26px;
    padding: 3px;
    border-radius: 26px;
    background: #aeb4bf;
    transition: background 75ms linear; }
    .oknf-switch-decoration::before {
      content: " ";
      display: block;
      width: 20px;
      height: 20px;
      background: #fff;
      border-radius: 50%;
      transition: transform 75ms linear; }
  .oknf-switch input:checked ~ .oknf-switch-decoration {
    background: #1fc174; }
    .oknf-switch input:checked ~ .oknf-switch-decoration::before {
      transform: translateX(17px); }
  .oknf-switch--has-bg {
    min-width: 250px;
    height: 46px;
    padding-top: 10px;
    padding-left: 66px;
    background: rgba(0, 0, 0, 0.05);
    border-radius: 3px; }
    .oknf-switch--has-bg .oknf-switch-decoration {
      top: 10px;
      left: 10px; }
