@keyframes rotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.loader {
  position: absolute;
  top: calc(50% - 20px);
  left: calc(50% - 20px);
  width: 40px;
  height: 40px;
  border-top: 3px solid rgba(76, 123, 217, 0.7);
  border-right: 3px solid transparent;
  border-radius: 50%;
  animation: rotate 800ms linear infinite; }
